import vueChannel from "vue-channel";
import { firestore } from "./firebase.js";
import { db } from "./global.js";

if (!db.collection) {
    db.collection = {};
}

function sendUpdate() {
    vueChannel("collection")
        .set({
            collection: db.collection
        });

    backupOnline();
}

function backupOnline() {
    console.log("TRYING");
    return new Promise((res, rej) => {
        vueChannel("user")
            .disposable(user => {
                console.log(user);
                if (user.uid) {
                    firestore
                        .collection("users")
                        .doc(user.uid)
                        .collection("data")
                        .doc("cardCollection")
                        .set({ collection: db.collection })
                        .then(() => {
                            res("store/success");
                        })
                        .catch(err => {
                            console.log(err);
                            rej("store/error");
                        });
                } else {
                    rej("auth/noUser");
                }
            });

        if (!vueChannel("user").state.uid) {
            rej("auth/noUser");
        }
    })
}

