function trimCard(card) {
    return {
        name: card.name,
        id: card.id,
        oracle_id: card.oracle_id,
        set_name: card.set_name,
        image_uris: {
            normal: card.image_uris.normal,
            small: card.image_uris.small
        },
        oracle_text: card.oracle_text
    }
}

function addToCollection(card, amountToAdd = 1) {
    var collection = db.collection;

    if (collection[card.id]) {
        collection[card.id].MagicManager.count += amountToAdd;
    } else {
        collection[card.id] = {
            ...trimCard(card),
            MagicManager: {
                count: amountToAdd
            }
        }
    }

    loadFullCard(card.id);
    
    db.collection = collection;
    sendUpdate()
}

function removeFromCollection(card, removeAmount = 1) {
    var collection = db.collection;

    if (collection[card.id]) {
        collection[card.id].MagicManager.count -= removeAmount;

        if (collection[card.id].MagicManager.count <= 0) {
            delete collection[card.id];

            db.collection[card.id] = null;
        }
    }

    db.collection = collection;
    sendUpdate()
}

function getList() {
    var collection = db.collection;

    var collectionArray = Object.values(collection);
    return collectionArray;
}

function searchCardId(id) {
    var collection = db.collection;
    var collectionArray = Object.values(collection);

    return collectionArray.filter(card => card.oracle_id === id);
}

function loadFullCard(id) {
    return new Promise((respond, reject) => {
        if (db[id]) {
            respond(db[id]);
        } else {
            fetch("https://api.scryfall.com/cards/" + id)
                .then(r => r.json())
                .then(response => {
                    db[id] = {
                        ...response,
                        MagicManager: {
                            timeToLive: 86400000,
                            storedAt: Date.now()
                        }
                    };

                    respond(response);
                })
                .catch(reject);
        }
    });
}

window.collectionManager = {
    addToCollection,
    removeFromCollection,
    getList,
    searchCardId,
    backupOnline,
    loadFullCard,
    trimCard
}

export {
    addToCollection,
    removeFromCollection,
    getList,
    searchCardId,
    backupOnline,
    loadFullCard
}