<template>
    <div class="cardDetails">
        <img :src="internalCard.image_uris.normal" alt="">
        <h2>{{ internalCard.name }}</h2>
        <h3>{{ internalCard.type_line }}</h3>
        <div class="collectionControlls" v-if="showCollectionControlls">
            <p>You own {{ ownAmount }}x {{ internalCard.name }} from this set ({{ totalOwnAmount }} in total from sets: {{ ownedSets }})</p>
            <button  @click="addToCollection(internalCard)">Add to collection</button>

            <button  @click="removeFromCollection(internalCard)">Remove from collection</button>
        </div>
        <div class="detailedInformation">
            <p v-html="text"></p>
            <div class="detailSection" v-if="trimmedCard === false">
                <p>
                    <b>Price </b>
                    <span v-if="internalCard.prices.eur">&euro;{{ internalCard.prices.eur }} </span>
                    <span v-if="internalCard.prices.usd">&dollar;{{ internalCard.prices.usd }} </span>
                </p>
                <p><b>Legal in</b> {{ legalList(internalCard) }}</p>
            </div>
            <div class="detailSection scrollable" v-if="showPrints">
                <div class="row clickable" :class="{ selected: internalCard.id === print.id }" v-for="print in prints" :key="print.id" @click="loadPrint(print)">
                    <p>{{ print.set_name }}</p>
                </div>
                <div class="row clickable" v-if="morePrintsAvailable" @click="loadMorePrints()">
                    <p>Load more</p>
                </div>
            </div>
        </div>
        <snackbar name="addCardToCollection">
            Added to your collection
        </snackbar>
        <snackbar name="removeCardFromCollection">
            Removed card from collection
        </snackbar>
    </div>
</template>

<script>
import parseMana from "../js/parseMana.js";
import { performAction, snackbar, textBox } from "../components.js";
import { replaceAll, db } from "../js/global.js";
import { addToCollection, removeFromCollection, searchCardId } from "../js/collection.js";

export default {
    props: ["card", "showCollectionControlls", "showPrints"],
    components: {
        snackbar,
        textBox
    },
    data() {
        return {
            prints: [],
            allPrints: [],
            morePrintsAvailable: false,
            morePrintPagesAvailable: false,
            printsCurrentPage: 1,
            internalCard: {
                name: "",
                id: "",
                type_line: "",
                oracle_text: "",
                prices: {
                    eur: null,
                    usd: null
                },
                legalities: {},
                image_uris: {
                    normal: ""
                }
            },
            amountToAdd: 1,
            amountToRemove: 1,
            owned: 0,
            update: 0
        }
    },
    watch: {
        card() {
            this.internalCard = Object.assign({}, this.internalCard, this.card);
        }
    },
    computed: {
        text() {
            let text = parseMana(this.internalCard.oracle_text);
            return replaceAll(text, "\n", "<br />");
        },
        ownAmount() {
            this.update;
            if (db.collection && db.collection[this.internalCard.id]) {
                return db.collection[this.internalCard.id].MagicManager.count;
            } else {
                return 0;
            }
        },
        totalOwnAmount() {
            this.update;

            if (db.collection) {
                var matchedCards = searchCardId(this.internalCard.oracle_id);
                
                if (matchedCards.length > 0) {
                    return matchedCards.map(card => card.MagicManager.count).reduce((current, entry) => current + entry);
                } else {
                    return 0;
                }
            } else {
                return 0;
            }
        },
        ownedSets() {
            this.update;

            if (db.collection) {
                var matchedCards = searchCardId(this.internalCard.oracle_id);
                return matchedCards.map(card => card.set_name).join(", ");
            } else {
                return "none";
            }
        },
        trimmedCard() {
            return this.card.trimmed ? true : false;
        }
    },
    methods: {
        legalList(card) {
            var entries = Object.entries(card.legalities);

            var list = [];

            entries.forEach(ruling => {
                if (ruling[1] === "legal") {
                    list.push(ruling[0]);
                }
            });

            return list.join(", ");
        },
        loadPrint(print) {
            this.internalCard = Object.assign({}, this.internalCard, print);
        },
        async loadMorePrints() {
            this.prints = [
                ...this.prints,
                ...this.allPrints.splice(0, 15)
            ]

            if (this.allPrints.length > 0) {
                this.morePrintsAvailable = true;
            } else {
                if (this.morePrintPagesAvailable) {
                    var prints = await fetch(this.internalCard.prints_search_uri).then(r => r.json());
                    this.morePrintPagesAvailable = prints.has_more;
                    this.allPrints = prints.data;
                } else {
                    this.morePrintsAvailable = false;
                }
            }
        },
        addToCollection(card) {
            addToCollection(card, this.amountToAdd);
            this.update = Math.random();
        },
        removeFromCollection(card) {
            removeFromCollection(card, this.amountToRemove);
            this.update = Math.random();
        }
    },
    async created() {
        this.internalCard = Object.assign({}, this.internalCard, this.card);

        var prints = await fetch(this.internalCard.prints_search_uri).then(r => r.json());
        this.morePrintPagesAvailable = prints.has_more;
        this.allPrints = prints.data;

        this.loadMorePrints();
    }
}
</script>

<style lang="scss" scoped>

@import "../mdl-vue/config.scss";

@font-face {
    font-family: "Ringbearer";
    src: url(../fonts/Ringbearer.ttf);
}

@font-face {
    font-family: "Literata";
    font-weight: 400;
    src: url(../fonts/Literata-Regular.ttf);
}

@font-face {
    font-family: "Literata";
    font-weight: 700;
    src: url(../fonts/Literata-Bold.ttf);
}

.cardDetails {
    width: 100%;

    img {
        max-width: 256px;

        margin: 16px auto;
        display: block;

        box-shadow: $elevation24dp;
        border-radius: 12px;
    }

    h2 {
        font-family: "Ringbearer";
        font-size: dpToRem(32);
        text-align: center;
    }

    h3 {
        margin-top: 0;

        font-family: "Literata";
        font-size: dpToRem(28);
        text-align: center;
        font-weight: 700;
        color: #555555;
    }

    p {
        font-family: "Literata";
        font-weight: 400;
    }

    .detailedInformation {
        max-width: 384px;

        margin: 0 auto;

        .detailSection {
            border: 2px solid #303030;
            border-radius: 5px;
            padding: 16px 0;
            margin: 16px 0;

            p {
                padding: 0 16px;
            }

            &.scrollable {
                max-height: 256px;
                overflow-y: auto;
            }

            .row {
                height: 32px;
                width: 100%;

                border-top: 1px solid #cecece;
                border-bottom: 1px solid #cecece;

                & + .row {
                    border-top: 0;
                }

                &.clickable {
                    cursor: pointer;
                }

                &.selected {
                    background-color: #abff73;
                }

                p {
                    margin: 0;
                    position: relative;
                    top: 50%;
                    transform: translateY(-50%);
                }
            }
        }
    }

    .collectionControlls {
        max-width: 384px;
        margin: 0 auto;

        .textBox {
            margin: 16px auto;
        }
    }
}

</style>